/* eslint-disable @typescript-eslint/no-explicit-any */

import * as React from 'react';
import { useEffect, useState, useCallback, useMemo } from 'react';
import { Link } from 'gatsby';
import throttle from 'lodash/throttle';

import './postList.scss';

interface PostListProps {
  posts: any[];
}

const PostList = (props: PostListProps) => {
  const { posts } = props;
  const [showCnt, setShowCnt] = useState(10);
  const [currentPostList, setCurrentPostList] = useState<JSX.Element[]>([]);
  const sortedPosts = useMemo(() => {
    posts.sort((a: any, b: any) => {
      const af = a.node.frontmatter;
      const bf = b.node.frontmatter;

      const aDate = new Date(hasUpdate(af.update) ? af.update : af.date);
      const bDate = new Date(hasUpdate(bf.update) ? bf.update : bf.date);

      if (aDate < bDate) return 1;
      if (aDate > bDate) return -1;
      return 0;
    });
    return posts;
  }, [posts]);

  const throttleScrollHandler = useCallback(
    throttle(() => {
      if (
        window.outerHeight >
        (document.querySelector(
          '.post-list'
        ) as HTMLDivElement).getBoundingClientRect().bottom
      ) {
        setShowCnt((prev: number) => {
          if (prev >= sortedPosts.length) return prev;
          return prev + 10;
        });
      }
    }, 250),
    [sortedPosts]
  );

  const expendPostList = useCallback((list: any) => {
    const mapToList = list.map((post: any) => {
      const { node } = post;
      const { fields, frontmatter } = node;
      const { slug } = fields;
      const { date, title, description, tags } = frontmatter;
      let { update } = frontmatter

      if (!hasUpdate(update)) {
        update = null;
      }

      const mapTag = tags.map((tag: string) => {
        if (tag === 'undefined') return;

        return (
          <div key={`${slug}-${tag}`} className="tag">
            <span>
              <Link to={`/tags#${tag}`}>{`#${tag}`}</Link>
            </span>
          </div>
        );
      });

      return (
        <li key={slug} className={`post`}>
          <article>
            <h1>
              <Link to={slug}>{title}</Link>
            </h1>
            <div className="info">
              <div className="date-wrap">
                <span className="date">{date}</span>
                {update ? (
                  <span className="update">&nbsp;{`(Updated: ${update})`}</span>
                ) : null}
              </div>
              {tags.length && tags[0] !== 'undefined' ? (
                <span className="info-dot">Â·</span>
              ) : null}
              <ul className="tag-list">{mapTag}</ul>
            </div>
            <Link to={slug} className="excerpt">
              <p>{description}</p>
            </Link>
          </article>
        </li>
      );
    });

    setCurrentPostList((prev: JSX.Element[]) => {
      return [...prev, ...mapToList];
    });
  }, []);

  useEffect(() => {
    if (showCnt > 0 && showCnt !== 10)
      expendPostList(sortedPosts.slice(currentPostList.length, showCnt));
  }, [showCnt]);

  useEffect(() => {
    if (currentPostList.length) setCurrentPostList([]);

    setShowCnt((prev: number) => {
      if (prev === 10) expendPostList(sortedPosts.slice(0, 10));
      return 10;
    });

    window.addEventListener('scroll', throttleScrollHandler);

    return () => {
      window.removeEventListener('scroll', throttleScrollHandler);
    };
  }, [sortedPosts]);

  return (
    <div className="post-list">
      <ul>{currentPostList}</ul>
    </div>
  );
};

function hasUpdate(update: string) {
  return !update.includes('0001');
}

export default PostList;
